const Tray = (props) => {
    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    
    return (
        <div>
            <Button onClick={handleOpen}> Open Draw</Button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby='draw-title'
                aria-describedby='draw-content'
            >
            
                        <SlidingDraw/>           
            </Modal>
        </div>
    )
}

export default Tray
