import React, {useState, useEffect} from 'react';
import Hero from "./components/Hero";
import Navbar from "./components/Navbar";


function App() {

     const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen)
    }
  return (
    <>
    <Navbar toggle={toggle}/>
    <Hero/>
    
    </>


  );
}

export default App;
