export const Dashboard = (props) => {

    const [anchorEl, setAnchorEl] = useState(false);
    const open = Boolean(anchorEl);
    const id = open ? 'dashboard' : undefined;
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null)
    };
 

    return (
      <>
            <Button 
                id={id}
                onClick={handleClick}
                className="dashboard-button"
                 sx = {{
                     display: 'block',
                     width: 200,
                     overflow: 'hidden',
                     color: 'white',
                     fontFamily : 'monospace',
                     fontSize: 20,
                 }}       
                >
                    Dashboard
                </Button>
                <Popover 
                    // id={id}
                    open={open}
                    anchorEl={anchorEl}
                    onClose={handleClose}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                    }}
                    // anchorPosition={
                    //     top: ,
                    //     left: ,
                    // }
                    >
                    <Box sx={{ flexGrow: 1}}>
                        <Grid container spacing={1}>
                            <Grid container item spacing={3}>
                                <FormRow1/>
                            </Grid>
                            <Grid container item spacing={3}>
                                <FormRow2/>
                            </Grid>
                            <Grid container item spacing={3}>
                                <FormRow3/>
                            </Grid>
                        </Grid>           
                    </Box>
                    </Popover>     
                </>
  
     
     
       
    )
}
