import React, { useState } from 'react'
import EnterButton from '@mui/material/Button';
import LoginButton from '@mui/material/Button';
import RegisterButton from '@mui/material/Button';
import Popover from '@mui/material/Popover';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';
const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

export const Enter = () => {
    const [anchorEl, setAnchorEl] = useState(false);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'enter' : undefined;

    return (
       <>
       <EnterButton 
       aria-describedby={id} 
       onClick={handleClick}
       sx = {{
        display: 'block',
        width: 200,
        overflow: 'hidden',
        color: 'white',
        fontFamily : 'monospace',
        fontSize: 20,
    }}

       
       >
        Enter
       </EnterButton>  
       <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'center'
        }}
        >
        <Box sx={{ flexGrow: 1}}>
        <Grid container spacing={2} columns={16}>
        <Grid item xs={8}>
            <Item>
            <LoginButton 
                     sx = {{
                        display: 'block',
                        width: 200,
                        overflow: 'hidden',
                        color: 'blue',
                        fontFamily : 'monospace',
                        fontSize: 20,
                        background: 'transparent',
                    }}
                onClick={() => {
                    alert('LOGIN ')
                }}
            >Login</LoginButton>
            </Item>
            </Grid>
            <Grid item xs={8}>
                <Item>
                <RegisterButton 
                   sx = {{
                    display: 'block',
                    width: 200,
                    overflow: 'hidden',
                    color: 'blue',
                    fontFamily : 'monospace',
                    fontSize: 20,
                }}
                onClick={() => {
                    alert('REGISTER ')
                }}
            >Register</RegisterButton>
            </Item>

            </Grid>
            </Grid>
            </Box> 

        </Popover>
       </>
    )
}
